import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { SessaoPage } from './sessao.page';

const routes: Routes = [
  {
    path: '',
    component: SessaoPage,
    children: [
      { path: '', redirectTo: 'geral' },
      { path: 'geral', loadChildren: './geral/sessao-geral.module#SessaoGeralPageModule' },
      { path: 'resposta', loadChildren: './respostas/sessao-resposta.module#SessaoRespostaPageModule' },
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes)
  ],
  declarations: [SessaoPage]
})
export class SessaoPageModule {}
